.App {
  text-align: center;
  background-color: #121213;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}
@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
/* Carrucel */

.imgCarrucel{
  width: 100%;
  height: 500px;
  background-size: cover;
}
.letra{
  font-size: 4rem;
}

@media (max-width: 700px){
  .imgCarrucel{
    width: 100%;
    height: 100%;
    background-size: cover;
  }
  .letra{
    font-size: 23px;
    text-align: center;
  }
  .letro{
    font-size: 14px;
    text-align: left;
  }
  .navbarCard{
    display: flex;
    width: 72%;
  }
}
/* Inicio Media */

.App-header {
  margin: 6% 0%;
  background-color: #1b1b1de8;
  display: flex;
  justify-content: space-around;
  height: 750px;
}
.datas{
  width: 16rem;
  display: flex;
}
.navbarJJ{
  background-color: rgba(27, 27, 26, 0.76);
}
.container{
  color: rgb(155, 155, 155);
}
.datosPersonales{
  width: 850px;
  height: 400px;
  display: flex;
  align-items: flex-start;
  justify-content: flex-start;
  flex-direction: column;
}
.App-link {
  color: #61dafb;
}

@media (max-width: 700px){
  .App-header {
    margin: 6% 2%;
    background-color: #1b1b1de8;
    height: 950px;
    display: flex;
    justify-content: space-around;
    flex-wrap: wrap;
  }
  .datas{
    width: 10rem;
  }
  .text-dark{
    font-size: 14px;
  }
  .datosPersonales{
    width: 70%;
    height:80%;
    display: flex;
    justify-content: center;
    flex-direction: column;
  }
}


/* Card Two Media */

.cardTwo{
  margin: 4% 22%;
  width: 38rem;
  height: 15rem;
  background-color: aliceblue;
  display: flex;
  border-radius: 1rem;
}
.imgCardTwo{
  margin: 3%;
}
.imgCard{
  margin: 0% 4%;
  border-radius: 10rem;
  border: rgba(3, 3, 3, 0.452) solid 10px;
  width: 12rem; 
  height: 12rem;
}
.letrasTwo{
  
  margin: 0% 8%;
}
.RellenoCardTwo{
  width: 60%;
  margin: 5% 0px;
}
.RedesCardTwo{
margin: 2% 0px;
}

@media (max-width: 700px){
  .cardTwo{
    margin: 10% 1%;
    width: 22rem;
    height: 12rem;
    background-color: aliceblue;
    display: flex;
    border-radius: 1rem;
  }
  .imgCardTwo{
    margin: 0%;
    width: 30%;
    margin: 3%;
  }
  .imgCard{
    border-radius: 10rem;
    border: rgba(3, 3, 3, 0.452) solid 9px;
    width: 7rem; 
    height: 7rem;
  }
  .letrasTwo{
    width: 100%;
  }
  .letrasTwoC{
    font-size: 18px;
  }
  .RellenoCardTwo{
    margin: 2%;
    text-align: center;
    font-size: 13px;
    width: 55%;
  }
  .RedesCardTwo{
  margin: 0%;
  }
}